/* Basic setup */
.App {
  font-family: "serif", sans-serif;
  background-color: transparent; /* Dark background */
  color: #f2f2f2; /* Light text */
}

/* Header */
.App-header {
  font-size: 2.5em;
  text-align: center;
  /* padding: 1em; */
  background: transparent; /* Slightly lighter than the main background */
  color: black;
}

.about-blurb p {
  font-size: 1em;
  text-align: center;

  padding: 1em;
  background: transparent; /* Slightly lighter than the main background */
  color: black;
}

/* Cover Image */
.cover-image-section {
  height: 30vh; /* 30% of the viewport height */
}

.cover-image {
  background-image: url("./assets/images/Exhibition.jpg");
  background-size: cover;
  background-position: center;
  height: 100%;
}

body {
  /* Base color */

  background-color: #775d47;
  background: linear-gradient(to top, #602f30, transparent),
    url(./assets/images/grainsvg.svg);
}

/* Gallery */
.image-gallery {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
}

.image-gallery > li {
  flex-basis: 350px; /* width: 350px; */
  list-style-type: none;
}

.image-gallery li img {
  object-fit: cover;
  max-width: 100%;
  height: auto;
  vertical-align: baseline;
  border-radius: 10px;
}

.image-gallery li {
  transition: transform 0.3s ease-in-out; /* Smooth transition */
}

.image-gallery li:hover {
  transform: scale(1.05); /* Increase size to 110% when hovered */
}

/* General styles for the About section */
.about-section h1 {
  padding-top: 15px;
  border-top: #8d7b68 2px solid;
  text-align: center;
}

.about-section {
  background: #8d7b68; 
}

/* Flexbox container for image and text */
.about-content {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  background: #8d7b68; /* Slightly lighter than the main background */
}

/* Image div */
.about-image img {
  max-width: 100%; /* To ensure the image doesn't exceed its container */
  height: auto;
  padding: 10px;
  margin: 20px;
  filter: saturate(0);
  border: #495734 2px solid;
  width: 500px;
}

/* Text div */
.about-text {
  flex: 1; /* Take up all available space */
  padding-left: 0px; /* Spacing between the image and text */
  padding-right: 40px;
}

/* Responsive design: Stacking for smaller screens */
@media (max-width: 768px) {
  .about-content {
    flex-direction: column;
    align-items: center;
  }
  .about-text {
    padding-left: 0; /* Reset padding */
    padding: 20px;
    text-align: left; /* Optional: Center-align text for smaller screens */
  }
  .about-blurb p {
    display: none;
  }
}

/* Contact */
.contact-section {
  font-size: 1.5em;
  text-align: center;
  padding: 1em;
}

.contact-section a {
  padding: 10px;
}


/* Contact */
.contact-section {
  font-size: 1.5em;
  text-align: center;
  padding: 1em;
  background-color: #602F30; /* Dark background to fit the artsy vibe */
}

.contact-section a {
  padding: 10px 20px;
  margin: 0 10px;
  text-decoration: none;
  color: #FFF; /* White text */
  border: 2px solid #FFF; /* White border */
  background-color: rgba(0, 0, 0, 0.2); /* Slightly transparent background */
  transition: all 0.3s ease; /* Transition for hover effects */

 
}

.contact-section a:hover {
  background-color: #FFF; /* White background */
  color: #602F30; /* Dark text */
  border: 2px solid #602F30; /* Dark border */
}
